/* 
    STYLESHEET FOR LARGER DEVICES
    -----------------------------
*/

.Title {
    display: grid;
    -webkit-align-content: center;
            align-content: center;
    background-image:url(/weather-app/static/media/Background.8dfaebb1.jpg);
    opacity: 0.9;
    text-align: center;
    color:white;        
    font-family: 'Times New Roman', Times, serif;
    font-size: 1.3em;
    font-weight: bold;
}

/* 
    STYLESHEET FOR SMALLER DEVICES
    -----------------------------   
*/

@media only screen and (max-width:750px) {

.Title {
    font-size: 1em
}
}
/* 
    STYLESHEET FOR LARGER DEVICES
    -----------------------------
*/

.Form {
    background-color: #35464f;
    display: grid;
    -webkit-justify-content: center;
            justify-content: center;
    padding-top: 4%;
}

.inputs {
    border: none;
    color: white;
    background: transparent;
    outline:none;
    border-bottom: 2px solid orangered;
    font-family: 'Times New Roman', Times, serif;
    font-weight: bolder;
    font-size: 1.1em;
    text-align: center; 
}



.button {
    position: relative;
    background-color: orangered;
    font-family: 'Times New Roman', Times, serif;
    border: 2px solid white;
    border-radius: 50px;
    outline: none;
    color: white;
    min-height: 8%;
    min-width: 50%;
    left: 25%;
}

/* 
    STYLESHEET FOR SMALLER DEVICES
    -----------------------------   
*/

@media only screen and (max-width:750px) {

.inputs {
    font-size: 0.95em;
    font-weight: 550;
}

}
/* 
    STYLESHEET FOR LARGER DEVICES
    -----------------------------
*/

.results {
    font-family: 'Times New Roman', Times, serif;
    font-size: 1.1em;
    font-weight: bold;
    color:white;
    margin-left: 2%;
}

/* 
    STYLESHEET FOR SMALLER DEVICES
    -----------------------------   
*/

@media only screen and (max-width:750px) {
.results {
    margin:0px;
    padding-left: 2%;
    font-size: 1em;
    background-color: #36454f;
    font-weight: 550;
}
}


/* 
    STYLESHEET FOR LARGER DEVICES
    -----------------------------
*/

.Main {
    display:grid;
    grid-template-columns:2fr 4fr 4fr 2fr;
    grid-template-rows: 2fr 4fr 4fr 2fr;
    grid-gap: 10px;
    background:-webkit-linear-gradient(left , red , orange);
    background:linear-gradient(to right , red , orange);
    background-repeat: no-repeat;
    background-size: cover;
    opacity: .9;
    height: 100vh;
}

.title {
    display:grid;
    grid-column:2/3;
    grid-row: 2/4;
}

.form {
    display:grid;
    grid-column:3/4;
    grid-row: 2/4;
}

.description {
    display: grid;
    grid-column:3/4;
    grid-row:3/4; 
}

/* 
    STYLESHEET FOR SMALLER DEVICES
    -----------------------------   
*/

@media only screen and (max-width:750px) {

.Main {
    display:grid;
    grid-template-columns:1fr 6fr 6fr 1fr;
    grid-template-rows: 1fr 8fr 6fr 8fr 1fr;
}

.title {
    display:grid;
    grid-column:2/4;
    grid-row: 2/3;
}

.form {
    display:grid;
    grid-column:2/4;
    grid-row: 3/4;
}

.description {
    display: grid;
    grid-column:2/4;
    grid-row: 4/5; 
}

}



body {
    margin: 0px;
}
